@use "../../assets/sass/variables" as *;
$blue: rgb(88, 40, 199);

.log-form {
  background: #fff;
  margin:  auto;
  box-shadow: 0px 2px 5px rgba(0,0,0,.25);
  position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgb(0,0,0,0.4);
    &__content {
      background-color: $accent-colour-two;
      margin: 15% auto;
      padding: 20px;
      border-radius: 10px;
      width: 40%;
      min-width: 320px;
      max-width: 475px;
  }
  &__cross {
    float: right;
    background-color: transparent;
}
&__cross:hover,
&__cross:focus {
    cursor: pointer;
}
  
  form {
    display: block;
    width: 100%;
    padding: 2em;
  }
  
  h2 {
    width: 100%;
    color: $heading-colour;
    font-family: $font-heading;
    font-size: 1.35em;
    display: block;
    background: $accent-colour-one;
    width: 100%;
    text-transform: uppercase;
    padding: .75em 1em .75em 1.5em;
    box-shadow:inset 0px 1px 1px fadeout(white, 95%);
    margin: 0;
    font-weight: 200;
  }
  ::placeholder {
    color: $body-colour;
    opacity: 0.5;
  }
  
  input {
    display: block;
    margin: auto auto;
    width: 100%;
    margin-bottom: 2em;
    padding: .5em 0;
    border: none;
    border-bottom: 1px solid $heading-colour;
    padding-bottom: 1.25em;
    color: $body-colour;
    
    &:focus {
      outline: none;
    }
  }
  
  .btn {
    display: inline-block;
    background: $accent-colour-two;
    border: none;
    border-radius: 10px;
    font-family: $font-secondary;
    font-size: larger;
    padding: .5em 2em;
    color: $heading-colour;
    margin-right: .5em;
    box-shadow: inset 0px 1px 0px fadeout(white, 80%); 
    &:hover {
      background: lighten($blue, 5%);
    }
    &:active {
      background: $blue; 
      box-shadow: inset 0px 1px 1px fadeout(black, 90%); 
    }
    &:focus {
      outline: none;
    }
  }
}

